  .homepage {
    display: flex;
    height: 100vh;
    justify-content: space-between;
    background-color: var(--third-color);
  }

  .homepage-headshot {
      width: 60%;
      height: auto;
      object-fit: cover;
  }


  .home-container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 32%;
    height: 175px;
  }
  
  .home-header {
    color: var(--first-color);
    font-size: 50px;
    font-family: 'Rato', sans-serif;
    text-decoration: none;
    font-weight: lighter;
    text-align: left;
    margin-top: 30%;
  }
  
  .home-header:after {
    content: "";
    width: 100%;
    height: 100%;
    line-height: 60px;
    animation: abomination1 7.5s linear 1;
    animation-fill-mode: forwards;
  }
  
  
    @keyframes abomination1 {
    0% {
        content: "";
    }
    2% {
      content: "I'm";
    }
    4% {
